


































import TimeEntry from '@/types/archive';
import Vue from 'vue';

export default Vue.extend({
  name: 'Bookmarks',
  components: {},
  props: {},
  data() {
    return {};
  },
  computed: {
    bookmarks(): Array<TimeEntry> {
      return this.$store.getters.bookmarks;
    },
    hasBookmarks(): boolean {
      return this.bookmarks.length > 0;
    },
  },
  methods: {
    deleteBookmark(bookmark: TimeEntry) {
      this.$store.dispatch('deleteTime', bookmark);
    },
    useBookmark(bookmark: TimeEntry) {
      this.$store.dispatch('setDose', bookmark.dose);
      this.$store.dispatch('setRatio', bookmark.ratio);
      this.$store.dispatch('setGrind', bookmark.grind);

      this.$router.push('Stopwatch');
    },
  },
});
