































































































































































import Vue from 'vue';
import SaveRecipe from '@/components/SaveRecipe.vue';
import TimeEntry from '@/types/archive';

export default Vue.extend({
  name: 'Doser',
  components: {
    SaveRecipe,
  },
  data() {
    return {
      addBeanModalIsOpen: false,
      doseStep: 0.5,
      ratioStep: 0.1,
      grindStep: 0.1,
      durationStep: 1,
    };
  },
  props: {
  },
  computed: {
    dose(): number {
      return this.$store.getters.getDose;
    },
    ratio(): number {
      return this.$store.getters.getRatio;
    },
    yield(): number {
      return this.$store.getters.getYield;
    },
    duration(): number {
      return this.$store.getters.getDuration;
    },
    grind: {
      get() {
        return this.$store.getters.getGrind;
      },
      set(val: string) {
        this.setGrind(parseFloat(val));
      },
    },
    beans() {
      return this.$store.getters.getBeans;
    },
    currentTimeEntry(): TimeEntry {
      return new TimeEntry({
        id: Math.random(),
        date: Date.now(),
        time: this.duration,
        rating: 0,
        isBookmark: true,
        dose: this.dose,
        ratio: this.ratio,
        grind: this.grind,
        name: '',
      });
    },
    recipeName(): string {
      const bookmark = this.$store.getters.findBookmarkByRecipe({
        dose: this.dose,
        ratio: this.ratio,
        grind: this.grind,
      });

      if (bookmark) {
        return bookmark.name;
      }

      return '';
    },
    formattedDose(): string {
      return `${this.dose.toFixed(1)}g`;
    },
    formattedRatio(): string {
      return `1:${this.ratio.toFixed(1)}`;
    },
    formattedYield(): string {
      return `${this.yield.toFixed(1)}g`;
    },
    formattedGrind(): string {
      return `${this.grind.toFixed(1)}`;
    },
    formattedDuration(): string {
      return `${this.duration.toFixed(0)}s`;
    },
  },
  methods: {
    increaseDose() {
      const newDose = this.dose + this.doseStep;

      if (newDose < 30) {
        this.$store.dispatch('setDose', newDose);
      }
    },
    decreaseDose() {
      const newDose = this.dose - this.doseStep;

      if (newDose > 0) {
        this.$store.dispatch('setDose', newDose);
      }
    },
    increaseRatio() {
      const newRatio = this.ratio + this.ratioStep;

      if (newRatio < 4) {
        this.$store.dispatch('setRatio', newRatio);
      }
    },
    decreaseRatio() {
      const newRatio = this.ratio - this.ratioStep;

      if (newRatio > 0) {
        this.$store.dispatch('setRatio', newRatio);
      }
    },
    increaseGrind() {
      const newGrind = this.grind + this.grindStep;

      if (newGrind <= 50) {
        this.$store.dispatch('setGrind', newGrind);
      }
    },
    decreaseGrind() {
      const newGrind = this.grind - this.grindStep;

      if (newGrind > 0) {
        this.$store.dispatch('setGrind', newGrind);
      }
    },
    increaseDuration() {
      const newDuration = this.duration + this.durationStep;

      if (newDuration <= 35) {
        this.$store.dispatch('setDuration', newDuration);
      }
    },
    decreaseDuration() {
      const newDuration = this.duration - this.durationStep;

      if (newDuration > 15) {
        this.$store.dispatch('setDuration', newDuration);
      }
    },
    setGrind(val: number) {
      this.$store.dispatch('setGrind', val);
    },
  },
});
