





































import { TimeEntryInterface } from '@/types/archive';
import Vue from 'vue';

export default Vue.extend({
  name: 'SaveRecipe',
  components: {},
  props: {
    buttonText: {
      type: String,
      default: 'Save recipe',
    },
    entry: {
      type: Object as () => TimeEntryInterface,
      required: true,
    },
  },
  data() {
    return {
      presetName: '',
    };
  },
  methods: {
    saveCurrentRecipe() {
      if (!this.presetName) return;

      this.entry.isBookmark = true;
      this.entry.name = this.presetName;
      this.$store.dispatch('addArchiveItem', this.entry);
    },
  },
  computed: {

  },
});
